import {
  AllorsBrandConnection,
  AllorsProductCategoryConnection,
  AllorsUnifiedGoodConnection,
  AllorsBrand,
  AllorsProductCategory,
  AllorsUnifiedGood,
} from '../../graphql-types';

export interface SearchResults {
  brands: AllorsBrand[];
  productCategories: AllorsProductCategory[];
  products: AllorsUnifiedGood[];
}

export class SearchEngine {
  constructor(
    private allAllorsBrand: AllorsBrandConnection,
    private allAllorsProductCategory: AllorsProductCategoryConnection,
    private allAllorsUnifiedGood: AllorsUnifiedGoodConnection,
  ) {}

  execute(term: string): SearchResults {
    var words = term ? term.toLowerCase().match(/\S+/g) : [];

    function contains(value: string): boolean {
      if (!value) {
        return false;
      }

      for (var word of words) {
        if (value.indexOf(word) < 0) {
          return false;
        }
      }

      return true;
    }

    return {
      brands: this.allAllorsBrand.nodes.filter((v) => contains(v.name?.toLowerCase())),
      productCategories: this.allAllorsProductCategory.nodes.filter((v) => contains(v.name?.toLowerCase())),
      products: this.allAllorsUnifiedGood.nodes.filter((v) => contains(v.name?.toLowerCase())),
    };
  }
}
