import React, { useContext } from 'react';
import { graphql } from 'gatsby';
import Img from 'gatsby-image';
import { FormattedMessage, useIntl, Link } from 'gatsby-plugin-intl';
import { Query } from '../../graphql-types';
import { Layout } from '../components/layout';
import { SEO } from '../components/seo';
import Card from 'react-bootstrap/Card';
import { SearchContext } from '../store/Search';
import { SearchEngine } from '../store/SearchEngine';

const SearchPage: React.FunctionComponent<{ data: Query }> = ({
  data: { allAllorsBrand, allAllorsProductCategory, allAllorsUnifiedGood },
}) => {
  const intl = useIntl();
  const {
    term: [term],
  } = useContext(SearchContext);

  const searchEngine = new SearchEngine(allAllorsBrand, allAllorsProductCategory, allAllorsUnifiedGood);
  const { brands, productCategories: allProductCategories, products } = searchEngine.execute(term);

  const productCategories = allProductCategories?.filter((v) => v.categoryImage);

  return (
    <Layout>
      <SEO title={intl.formatMessage({ id: 'search' })} description={intl.formatMessage({ id: 'search-description' })} />

      <main role="main">
        <article>
          <header className="page-header">
            <div className="container">
              <div className="row">
                <div className="col-12">
                  <nav aria-label="breadcrumb">
                    <ol className="breadcrumb">
                      <li className="breadcrumb-item">
                        <Link to="/">
                          <FormattedMessage id="home" />
                        </Link>
                      </li>
                      <li className="breadcrumb-item active" aria-current="page">
                        <FormattedMessage id="search" />
                      </li>
                    </ol>
                  </nav>
                  <h1 className="page-title">
                    <FormattedMessage id="search" />
                  </h1>
                  <p className="page-description">
                    {term && (
                      <>
                        <FormattedMessage id="search-results-for" /> {term}
                      </>
                    )}

                    {!term && (
                      <>
                        <FormattedMessage id="please-add-search-criteria" /> {term}
                      </>
                    )}
                  </p>
                  <div className="separator"></div>
                </div>
              </div>
            </div>
          </header>

          {term && brands?.length > 0 && (
            <div className="container">
              <div className="row">
                <div className="col-xl-9 col-lg-8 order-lg-2">
                  <div className="row">
                    {brands.map((v) => {
                      const fluid = v.logoImage.file.childImageSharp.fluid;
                      return (
                        <div key={v.slug} className="col-md-4 col-sm-6">
                          <Link to={`/${v.slug}`} key={v.slug} className="product-link">
                            <Card>
                              <Img fluid={fluid} />
                              <Card.Body>
                                <Card.Title>{v.name}</Card.Title>
                              </Card.Body>
                            </Card>
                          </Link>
                        </div>
                      );
                    })}
                  </div>
                </div>
                <div className="col-xl-3 col-lg-4 order-lg-1">
                  <div className="list-group mb-4">
                    <div className="list-group-item list-group-item-action active">Brands ({brands.length})</div>
                  </div>
                </div>
              </div>
            </div>
          )}

          {term && productCategories?.length > 0 && (
            <div className="container">
              <div className="row">
                <div className="col-xl-9 col-lg-8 order-lg-2">
                  <div className="row">
                    {productCategories.map((v) => {
                      const fluid = v.categoryImage?.file.childImageSharp.fluid;
                      return (
                        <div key={v.slug} className="col-md-4 col-sm-6">
                          <Link to={`/${v.slug}`} key={v.slug} className="product-link">
                            <Card>
                              {fluid && <Img fluid={fluid} />}
                              <Card.Body>
                                <Card.Title>{v.name}</Card.Title>
                              </Card.Body>
                            </Card>
                          </Link>
                        </div>
                      );
                    })}
                  </div>
                </div>
                <div className="col-xl-3 col-lg-4 order-lg-1">
                  <div className="list-group mb-4">
                    <div className="list-group-item list-group-item-action active">Product Categories ({productCategories.length})</div>
                  </div>
                </div>
              </div>
            </div>
          )}

          {term && products?.length > 0 && (
            <div className="container">
              <div className="row">
                <div className="col-xl-9 col-lg-8 order-lg-2">
                  <div className="row">
                    {products.map((v) => {
                      const fluid = v.primaryPhoto?.file.childImageSharp.fluid;
                      return (
                        <div key={v.slug} className="col-md-4 col-sm-6">
                          <Link to={`/${v.slug}`} key={v.slug} className="product-link">
                            <Card>
                              {fluid && <Img fluid={fluid} />}
                              <Card.Body>
                                <Card.Title>{v.name}</Card.Title>
                              </Card.Body>
                            </Card>
                          </Link>
                        </div>
                      );
                    })}
                  </div>
                </div>
                <div className="col-xl-3 col-lg-4 order-lg-1">
                  <div className="list-group mb-4">
                    <div className="list-group-item list-group-item-action active">Products ({products.length})</div>
                  </div>
                </div>
              </div>
            </div>
          )}
        </article>
      </main>
    </Layout>
  );
};

export const query = graphql`
  query {
    allAllorsBrand {
      nodes {
        name
        slug
        description
        logoImage {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
    allAllorsProductCategory {
      nodes {
        name
        slug
        description
        categoryImage {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
    allAllorsUnifiedGood {
      nodes {
        name
        slug
        description
        primaryPhoto {
          file {
            childImageSharp {
              id
              fluid {
                ...GatsbyImageSharpFluid
              }
            }
          }
        }
      }
    }
  }
`;

export default SearchPage;
